/* 锚样式 - 像素科幻风格，冷色调 */

/* ========== CSS 变量定义 ========== */
:root {
  /* 冷静克制色调 - 保持低饱和度，但极大提高亮度使其产生“发光”感 */
  --primary-cyan: hsl(190, 50%, 82%); /* 产生柔和的自发光感 */
  --primary-blue: hsl(210, 40%, 80%);
  --primary-indigo: hsl(225, 35%, 80%);
  --accent-teal: hsl(175, 40%, 78%);
  --accent-purple: hsl(265, 30%, 82%);
  --accent-green: hsl(145, 35%, 78%);
  --accent-red: hsl(0, 45%, 80%);
  --accent-orange: hsl(30, 50%, 80%);

  /* 深度背景 - 稍微调暗一点点以增加文字对比度 */
  --bg-primary: hsl(220, 15%, 12%);
  --bg-secondary: hsl(220, 12%, 16%);
  --bg-tertiary: hsl(220, 10%, 20%);
  --bg-card: hsl(220, 12%, 18%);
  --bg-glass: hsla(220, 15%, 14%, 0.85);

  /* 文字颜色 - 极致清晰明亮，彻底解决视觉疲劳 */
  --text-primary: #ffffff; /* 纯白，最高亮度 */
  --text-secondary: hsl(210, 25%, 96%); /* 显著提高亮度，接近白色 */
  --text-tertiary: hsl(210, 20%, 88%); /* 显著提高亮度 */

  /* 边框和阴影 - 极简主义，放弃发光效果 */
  --border-color: hsla(210, 20%, 60%, 0.15); /* 稍微加强边框可见度 */
  --border-strong: hsla(210, 20%, 70%, 0.25);
  --shadow-sm: 0 2px 4px hsla(0, 0%, 0%, 0.3);
  --shadow-md: 0 8px 24px hsla(0, 0%, 0%, 0.4);
  --shadow-lg: 0 16px 48px hsla(0, 0%, 0%, 0.5);
  --shadow-glow: none;

  /* UI 元素 - 扁平化且有质感 */
  --radius-xs: 2px;
  --radius-sm: 4px;
  --radius-md: 6px;
  --radius-lg: 10px;

  --panel-bg: var(--bg-card);
  --panel-bg-hover: hsl(220, 10%, 23%);

  /* 移除大部分渐变，改用纯色以增加“冷静”感 */
  --gradient-primary: none;
  --gradient-card: none;
  --gradient-glass: none;

  /* 动画时间 */
  --transition-fast: 0.15s;
  --transition-normal: 0.25s;
  --transition-slow: 0.35s;
}

/* ========== 全局样式 ========== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family:
    'Space Grotesk',
    'Noto Sans SC',
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    sans-serif;
  background: var(--bg-primary);
  color: var(--text-primary);
  font-size: 1rem; /* 调大字体 */
  line-height: 1.8; /* 增加行间距 */
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* 像素风格字体（可选） */
@font-face {
  font-family: 'Pixel';
  src: url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');
}

/* ========== 主应用容器 ========== */
.space-station-app {
  width: 100%;
  min-height: 100%;
  height: 100%;
  position: relative;
  color: var(--text-primary);
  overflow: hidden;
  background: var(--bg-primary);
}

/* 全屏模式支持 */
html:fullscreen .space-station-app,
html:-webkit-full-screen .space-station-app,
html:-moz-full-screen .space-station-app,
html:-ms-fullscreen .space-station-app {
  width: 100vw !important;
  height: 100vh !important;
}

/* ========== 屏幕容器 ========== */
.screen {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: none;
  opacity: 0;
  transition: opacity var(--transition-slow);
  overflow: hidden;
}

.screen.active {
  display: flex;
  opacity: 1;
  z-index: 10;
}

/* ========== 开始屏幕 ========== */
.start-screen {
  align-items: center;
  justify-content: center;
  background: radial-gradient(circle at 50% 50%, hsla(180, 100%, 40%, 0.15) 0%, transparent 70%), var(--bg-primary);
  flex-direction: column;
}

.start-screen-content {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  animation: fadeInUp 1s ease-out;
}

.start-screen-logo {
  font-size: 4rem;
  color: var(--primary-cyan);
  filter: drop-shadow(0 0 20px var(--primary-cyan));
  margin-bottom: 1rem;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0%,
  100% {
    transform: scale(1);
    filter: drop-shadow(0 0 20px var(--primary-cyan));
  }
  50% {
    transform: scale(1.05);
    filter: drop-shadow(0 0 30px var(--primary-cyan));
  }
}

.start-screen-title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--primary-cyan);
  font-family: 'Michroma', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  margin: 0;
}

.start-screen-subtitle {
  font-size: 0.75rem;
  color: var(--text-secondary);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  margin: 0;
}

.start-screen-button {
  margin-top: 2rem;
  padding: 0.75rem 2rem;
  background: var(--primary-cyan);
  border: 2px solid var(--primary-cyan);
  border-radius: 4px;
  color: var(--bg-primary);
  font-size: 0.9rem;
  font-weight: 600;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  transition: all var(--transition-normal);
  box-shadow: 0 0 20px hsla(180, 100%, 40%, 0.3);
}

.start-screen-button:hover {
  background: var(--accent-teal);
  border-color: var(--accent-teal);
  transform: translateY(-2px);
  box-shadow: 0 0 30px hsla(170, 100%, 35%, 0.5);
}

.start-screen-button:active {
  transform: translateY(0);
}

.start-screen-hint {
  margin-top: 1rem;
  font-size: 0.7rem;
  color: var(--text-tertiary);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  animation: blink 2s ease-in-out infinite;
}

@keyframes blink {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
}

/* ========== 标题页面 ========== */
.splash-screen {
  align-items: center;
  justify-content: center;
  background:
    radial-gradient(circle at 30% 30%, hsla(180, 100%, 40%, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 70% 70%, hsla(200, 100%, 35%, 0.1) 0%, transparent 50%), var(--bg-primary);
  flex-direction: column;
}

.splash-screen-content {
  width: 100%;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 2rem;
  animation: fadeInUp 0.8s ease-out;
}

.splash-header {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.splash-logo {
  font-size: 3rem;
  color: var(--primary-cyan);
  filter: drop-shadow(0 0 15px var(--primary-cyan));
}

.splash-title {
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--primary-cyan);
  font-family: 'Michroma', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  margin: 0;
}

.splash-subtitle {
  font-size: 0.7rem;
  color: var(--text-secondary);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  margin: 0;
}

.splash-actions {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  width: 100%;
  max-width: 300px;
}

.splash-button {
  padding: 0.75rem 1.5rem;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  color: var(--text-primary);
  font-size: 0.85rem;
  font-weight: 600;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  transition: all var(--transition-fast);
}

.splash-button.primary {
  background: var(--primary-cyan);
  border-color: var(--primary-cyan);
  color: var(--bg-primary);
}

.splash-button.primary:hover {
  background: var(--accent-teal);
  border-color: var(--accent-teal);
  box-shadow: 0 0 20px hsla(170, 100%, 35%, 0.4);
}

.splash-button.secondary:hover {
  background-color: var(--panel-bg-hover);
  border-color: var(--border-strong);
  box-shadow: var(--shadow-md);
}

.splash-button.icon-only {
  width: 40px;
  height: 40px;
  padding: 0;
  border-radius: 50%;
  margin: 0 auto;
}

.splash-button.icon-only:hover {
  background: var(--bg-tertiary);
  border-color: var(--primary-cyan);
  transform: scale(1.1);
}

.splash-footer {
  margin-top: auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.splash-copyright,
.splash-author {
  font-size: 0.65rem;
  color: var(--text-tertiary);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  margin: 0;
}

/* ========== 主游戏界面 ========== */
.main-screen {
  flex-direction: column;
  background:
    radial-gradient(circle at 20% 50%, hsla(195, 20%, 65%, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, hsla(225, 12%, 60%, 0.08) 0%, transparent 50%), var(--bg-primary);
}

.space-station-app::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* 更现代：淡网格 + 很轻微的舱壁扫描线（避免“像素小游戏”感） */
  background-image:
    linear-gradient(90deg, hsla(195, 20%, 65%, 0.02) 1px, transparent 1px),
    linear-gradient(0deg, hsla(195, 20%, 65%, 0.015) 1px, transparent 1px),
    repeating-linear-gradient(0deg, transparent 0 10px, hsla(0, 0%, 100%, 0.01) 10px 11px);
  background-size:
    72px 72px,
    72px 72px,
    100% 100%;
  background-position: center, center, center;
  pointer-events: none;
  z-index: 0;
}

/* ========== 统一的“舰体 HUD 面板”卡片质感 ========== */
:where(
  .status-card,
  .entry-item,
  .monitor-item,
  .detail-section,
  .maintext-container,
  .message-column,
  .chat-options-container,
  .chat-no-options-content,
  .setup-screen-content,
  .module-container
) {
  position: relative;
  isolation: isolate;
  background: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-sm);
  box-shadow: var(--shadow-sm);
}

:where(
  .status-card,
  .entry-item,
  .monitor-item,
  .detail-section,
  .maintext-container,
  .message-column,
  .chat-options-container,
  .chat-no-options-content,
  .setup-screen-content,
  .module-container
)::before {
  z-index: 2;
}

:where(
  .status-card,
  .entry-item,
  .monitor-item,
  .detail-section,
  .maintext-container,
  .message-column,
  .chat-options-container,
  .chat-no-options-content,
  .setup-screen-content,
  .module-container
)::after {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: inherit;
  pointer-events: none;
  z-index: 1;
  /* 极其轻微的顶部高光 */
  background: linear-gradient(180deg, hsla(0, 0%, 100%, 0.03), transparent 20%);
  opacity: 0.5;
}

:where(
    .status-card,
    .entry-item,
    .monitor-item,
    .detail-section,
    .maintext-container,
    .message-column,
    .chat-options-container,
    .chat-no-options-content,
    .setup-screen-content,
    .module-container
  )
  > * {
  position: relative;
  z-index: 3;
}

/* ========== 开局设置页面 ========== */
.setup-screen {
  align-items: center;
  justify-content: center;
  background:
    radial-gradient(circle at 30% 30%, hsla(180, 100%, 40%, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 70% 70%, hsla(200, 100%, 35%, 0.1) 0%, transparent 50%), var(--bg-primary);
  flex-direction: column;
}

.setup-screen-content {
  width: 100%;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding: 2rem;
  background:
    var(--gradient-glass) padding-box,
    var(--panel-border-gradient) border-box;
  border-radius: var(--radius-md);
  animation: fadeInUp 0.8s ease-out;
}

.setup-header {
  text-align: center;
}

.setup-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary-cyan);
  font-family: 'Michroma', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin: 0 0 0.5rem 0;
}

.setup-subtitle {
  font-size: 0.75rem;
  color: var(--text-secondary);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin: 0;
}

.setup-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.setup-form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.setup-label {
  font-size: 0.75rem;
  color: var(--text-secondary);
  font-weight: 600;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.setup-input,
.setup-select {
  padding: 0.5rem 0.75rem;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  color: var(--text-primary);
  font-size: 0.8rem;
  font-family: 'JetBrains Mono', 'Noto Sans SC', monospace;
  transition: all var(--transition-fast);
}

.setup-input:focus,
.setup-select:focus {
  outline: none;
  border-color: var(--primary-cyan);
  box-shadow: 0 0 0 2px hsla(195, 20%, 65%, 0.15);
}

.setup-actions {
  display: flex;
  gap: 0.75rem;
  justify-content: flex-end;
  margin-top: 0.5rem;
}

.setup-button {
  padding: 0.6rem 1.25rem;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  color: var(--text-primary);
  font-size: 0.8rem;
  font-weight: 600;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all var(--transition-fast);
}

.setup-button.primary {
  background: var(--primary-cyan);
  border-color: var(--primary-cyan);
  color: var(--bg-primary);
}

.setup-button.primary:hover {
  background: var(--accent-teal);
  border-color: var(--accent-teal);
  box-shadow: 0 0 20px hsla(170, 100%, 35%, 0.4);
}

.setup-button.secondary:hover {
  background-color: var(--panel-bg-hover);
  border-color: var(--border-strong);
  box-shadow: var(--shadow-sm);
}

/* ========== 主游戏界面内容 ========== */
.main-screen {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

/* ========== 顶部导航栏 ========== */
.main-header {
  background: var(--bg-secondary); /* 改用不透明背景 */
  border-bottom: 1px solid var(--border-color);
  box-shadow: var(--shadow-sm);
  position: sticky;
  top: 0;
  z-index: 100;
  padding: 0.5rem 0.75rem;
  flex-shrink: 0;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
  max-width: 1400px;
  margin: 0 auto;
}

.logo-section {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.logo-icon {
  font-size: 1.25rem;
  color: var(--primary-cyan);
  filter: drop-shadow(0 0 6px var(--primary-cyan));
}

.logo-text {
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--primary-cyan);
  letter-spacing: 0.1em;
  font-family: 'Michroma', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.header-status {
  display: flex;
  align-items: center;
  gap: 1rem;
}

/* ========== 全屏按钮样式 ========== */
.fullscreen-button {
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  padding: 0.4rem 0.6rem;
  color: var(--text-primary);
  cursor: pointer;
  transition: all var(--transition-fast);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
  min-width: 36px;
  height: 36px;
}

.fullscreen-button:hover {
  background-color: var(--panel-bg-hover);
  border-color: var(--border-strong);
  box-shadow: var(--shadow-sm);
  color: var(--primary-cyan);
  transform: scale(1.05);
}

.fullscreen-button:active {
  transform: scale(0.95);
}

.start-screen .fullscreen-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  z-index: 100;
}

.status-indicator {
  display: flex;
  align-items: center;
  gap: 0.4rem;
  padding: 0.3rem 0.6rem;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border-radius: var(--radius-xs);
  border: 1px solid transparent;
  font-size: 0.75rem;
}

.status-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: var(--accent-green);
  box-shadow: 0 0 3px var(--accent-green);
}

.status-indicator.online .status-dot {
  background: var(--accent-teal);
  box-shadow: 0 0 10px var(--accent-teal);
}

@keyframes pulse {
  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.7;
    transform: scale(1.1);
  }
}

.status-text {
  font-size: 0.7rem;
  color: var(--text-secondary);
  font-weight: 500;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
}

/* ========== 主内容区域 ========== */
.main-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  max-width: 100%;
  margin: 0 auto;
  width: 100%;
  padding: 0;
  gap: 0;
  position: relative;
  z-index: 1;
  overflow: hidden;
  min-height: 0;
}

/* ========== 顶部模块切换导航 ========== */
.top-nav-menu {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex: 1;
  justify-content: center;
}

.nav-item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem 1rem;
  background-color: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-xs);
  color: var(--text-secondary);
  font-size: 0.9rem;
  line-height: 1.6;
  cursor: pointer;
  transition: all var(--transition-fast);
  font-weight: 500;
  overflow: hidden;
  text-align: left;
  width: 100%;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
}

.nav-item::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: var(--primary-cyan);
  transform: scaleX(0);
  transition: transform var(--transition-normal);
}

.nav-item:hover {
  background-color: var(--panel-bg-hover);
  border-color: var(--border-strong);
  color: var(--text-primary);
}

.nav-item:hover::before {
  transform: scaleX(0.5); /* Hover shows a hint */
}

.nav-item.active {
  background-color: var(--bg-tertiary);
  border-color: var(--primary-cyan);
  color: var(--text-primary);
  box-shadow: var(--shadow-sm);
}

.nav-item.active::before {
  transform: scaleX(1);
}

.nav-shortcut {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 18px;
  height: 18px;
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  font-size: 0.65rem;
  font-weight: 600;
  color: var(--text-tertiary);
  margin-right: 0.25rem;
  transition: all var(--transition-fast);
}

.nav-item:hover .nav-shortcut,
.nav-item.active .nav-shortcut {
  background: var(--primary-cyan);
  border-color: var(--primary-cyan);
  color: var(--bg-primary);
}

.nav-icon {
  font-size: 1rem; /* 调大图标 */
  color: var(--primary-cyan);
  transition: transform var(--transition-fast);
  width: 16px;
  text-align: center;
}

.nav-item:hover .nav-icon,
.nav-item.active .nav-icon {
  color: var(--accent-teal);
}

.nav-text {
  flex: 1;
}

/* ========== 主内容区域 ========== */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* ========== 内容面板 ========== */
.content-panel {
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding: 0.75rem; /* 添加内边距 */
}

.module-container {
  display: none;
  background-color: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  overflow: hidden;
  flex: 1;
  flex-direction: column;
  min-height: 0;
}

.module-container.active {
  display: flex;
  animation: fadeInUp 0.4s ease-out;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.module-header {
  padding: 0.5rem 0.75rem;
  border-bottom: 1px solid var(--border-color);
  background: var(--bg-glass);
  flex-shrink: 0;
}

.module-title {
  font-size: 0.85rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-primary);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.module-title i {
  color: var(--primary-cyan);
}

/* ========== 主文本框样式 ========== */
.maintext-container {
  margin-bottom: 1.2rem;
  padding: 1rem;
}

.maintext-header {
  display: flex;
  align-items: center;
  margin-bottom: 0.8rem;
  padding-bottom: 0.6rem;
  border-bottom: 1px solid var(--border-color);
}

.maintext-title {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin: 0;
}

.maintext-title i {
  color: var(--primary-cyan);
}

/* 三个区域的容器 */
.maintext-areas {
  display: flex;
  gap: 0.75rem;
  align-items: stretch;
}

/* 每个区域的容器 */
.maintext-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0; /* 允许 flex 子元素收缩 */
}

.maintext-section-header {
  margin-bottom: 0.5rem;
  padding-bottom: 0.4rem;
  border-bottom: 1px solid var(--border-color);
}

.maintext-section-title {
  font-size: 0.7rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.maintext-section-area {
  flex: 1;
  min-height: 120px;
  max-height: 300px;
  overflow-y: auto;
  padding: 0.8rem;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-primary);
  font-size: 0.95rem; /* 调大字号，让阅读更舒适 */
  line-height: 2.0; /* 调宽行间距 */
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'JetBrains Mono', 'Noto Sans SC', monospace;
  /* 性能优化：使用 contain 属性限制渲染范围 */
  contain: content;
  overscroll-behavior: contain;
  -webkit-overflow-scrolling: touch;
  /* 性能优化：在滚动区域移除复杂的阴影和外发光 */
  box-shadow: var(--shadow-sm);
}

.maintext-section-area::-webkit-scrollbar {
  width: 6px;
}

.maintext-section-area::-webkit-scrollbar-track {
  background: var(--bg-tertiary);
  border-radius: 3px;
}

.maintext-section-area::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 3px;
}

.maintext-section-area::-webkit-scrollbar-thumb:hover {
  background: var(--primary-cyan);
}

/* 兼容旧格式：如果只有一个 maintext-area */
.maintext-area {
  min-height: 120px;
  max-height: 300px;
  overflow-y: auto;
  padding: 0.8rem;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-primary);
  font-size: 0.85rem;
  line-height: 1.6;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'Courier New', 'Consolas', 'Monaco', monospace;
}

.maintext-area::-webkit-scrollbar {
  width: 6px;
}

.maintext-area::-webkit-scrollbar-track {
  background: var(--bg-tertiary);
  border-radius: 3px;
}

.maintext-area::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 3px;
}

.maintext-area::-webkit-scrollbar-thumb:hover {
  background: var(--primary-cyan);
}

.module-body {
  padding: 0.75rem;
  flex: 1;
  overflow-y: auto;
  min-height: 0;
}

/* ========== 状态卡片 ========== */
.status-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 0.5rem;
  margin-bottom: 0.75rem;
}

.status-card {
  padding: 0.6rem 0.75rem;
  transition: all var(--transition-fast);
  position: relative;
  overflow: hidden;
}

.status-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--gradient-primary);
  transform: scaleX(0);
  transition: transform var(--transition-normal);
}

.status-card:hover {
  background-color: var(--panel-bg-hover);
  border-color: var(--border-strong);
  box-shadow: var(--shadow-md);
}

.status-card:hover::before {
  transform: scaleX(1);
}

.status-card-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.status-card-icon {
  font-size: 0.85rem;
  color: var(--primary-cyan);
  width: 14px;
}

.status-card-title {
  font-size: 0.75rem; /* 稍微调大 */
  font-weight: 600;
  color: var(--text-secondary);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.status-card-content {
  margin-top: 0.4rem;
}

.status-value {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.value-number {
  font-size: 1.1rem; /* 调大数值字号 */
  font-weight: 600;
  color: var(--primary-cyan);
  font-family: 'JetBrains Mono', 'Noto Sans SC', monospace;
}

.status-bar {
  width: 100%;
  height: 4px;
  background: var(--bg-tertiary);
  border-radius: 2px;
  overflow: hidden;
  position: relative;
  margin-top: 0.3rem;
}

.status-bar-fill {
  height: 100%;
  background: var(--primary-cyan);
  border-radius: 2px;
  transition: width var(--transition-slow);
  position: relative;
}

.status-bar-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent, hsla(0, 0%, 100%, 0.15), transparent);
  animation: shimmer 2s infinite;
}

.status-empty {
  padding: 2rem;
  text-align: center;
  color: var(--text-tertiary);
  font-size: 0.75rem;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
}

.status-detail {
  font-size: 0.7rem; /* 稍微调大 */
  color: var(--text-tertiary);
  margin-top: 0.3rem;
  line-height: 1.5;
}

.risk-low {
  color: var(--accent-green);
}

.risk-medium {
  color: var(--accent-orange);
}

.risk-high {
  color: var(--accent-red);
}

.status-bar-fill.status-low {
  background: var(--accent-green);
}

.status-bar-fill.status-medium {
  background: var(--accent-orange);
}

.status-bar-fill.status-high {
  background: var(--accent-red);
}

.status-bar-fill.status-healthy {
  background: var(--accent-green);
}

.status-bar-fill.status-warning {
  background: var(--accent-orange);
}

.status-bar-fill.status-critical {
  background: var(--accent-red);
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.status-indicator-small {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.status-dot-small {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--accent-teal);
}

.status-indicator-small.healthy .status-dot-small {
  background: var(--accent-teal);
}

/* ========== 状态详情 ========== */
.status-details {
  margin-top: 0.75rem;
}

.detail-section {
  padding: 0.75rem;
}

.detail-title {
  font-size: 0.75rem;
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.4rem;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.detail-title::before {
  content: '';
  width: 4px;
  height: 20px;
  background: var(--gradient-primary);
  border-radius: 2px;
}

.monitor-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 0.5rem;
}

.monitor-item {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  padding: 0.5rem 0.6rem;
  border-radius: var(--radius-xs);
  transition: all var(--transition-fast);
}

.monitor-item:hover {
  background-color: var(--panel-bg-hover);
  border-color: var(--border-strong);
  box-shadow: var(--shadow-sm);
}

.monitor-label {
  font-size: 0.65rem;
  color: var(--text-tertiary);
  font-weight: 500;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
}

.monitor-value {
  font-size: 0.95rem; /* 稍微调大 */
  font-weight: 600;
  color: var(--primary-cyan);
  font-family: 'JetBrains Mono', 'Noto Sans SC', monospace;
}

/* ========== 占位符内容 ========== */
.placeholder-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
  min-height: 400px;
}

.placeholder-icon {
  font-size: 4rem;
  color: var(--primary-cyan);
  margin-bottom: 1.5rem;
  opacity: 0.6;
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.placeholder-text {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.placeholder-subtext {
  font-size: 1rem;
  color: var(--text-secondary);
}

/* ========== 模态框 ========== */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: hsla(0, 0%, 0%, 0.7);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 0;
  transition: opacity var(--transition-normal);
}

.modal-overlay.active {
  display: flex;
  opacity: 1;
}

.modal-container {
  background: var(--gradient-glass);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid var(--border-color);
  border-radius: 16px;
  box-shadow: var(--shadow-lg);
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transform: scale(0.9);
  transition: transform var(--transition-normal);
}

.modal-overlay.active .modal-container {
  transform: scale(1);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  border-bottom: 1px solid var(--border-color);
  background: var(--bg-glass);
}

.modal-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
}

.modal-close {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all var(--transition-fast);
}

.modal-close:hover {
  background: var(--bg-tertiary);
  border-color: var(--primary-cyan);
  color: var(--text-primary);
  transform: rotate(90deg);
}

.modal-body {
  padding: 2rem;
  overflow-y: auto;
  flex: 1;
}

.modal-detail-content {
  color: var(--text-primary);
}

.modal-detail-content p {
  margin-bottom: 1rem;
  line-height: 1.6;
}

/* ========== 响应式设计 ========== */
@media (max-width: 1024px) {
  .main-content {
    flex-direction: column;
    padding: 0.5rem;
    gap: 0.5rem;
  }

  .header-content {
    flex-wrap: wrap;
    gap: 0.75rem;
  }

  .top-nav-menu {
    order: 3;
    width: 100%;
    justify-content: center;
    gap: 0.4rem;
    overflow-x: auto;
  }

  .nav-item {
    min-width: 100px;
    padding: 0.5rem 0.75rem;
    font-size: 0.85rem;
  }

  .nav-shortcut {
    width: 18px;
    height: 18px;
    font-size: 0.65rem;
  }

  .status-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 0.75rem;
  }

  .entry-item {
    padding: 0.75rem;
    gap: 0.75rem;
  }

  .entry-icon {
    width: 32px;
    height: 32px;
    font-size: 1rem;
  }
}

@media (max-width: 768px) {
  .header-content {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }

  .logo-text {
    font-size: 1rem;
  }

  .logo-icon {
    font-size: 1.5rem;
  }

  .status-grid {
    grid-template-columns: 1fr;
  }

  .monitor-grid {
    grid-template-columns: 1fr;
  }

  .entry-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  .entry-meta {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
}

/* ========== 滚动条样式 ========== */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: var(--bg-secondary);
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  background: var(--primary-cyan);
  border-radius: 5px;
  border: 2px solid var(--bg-secondary);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent-teal);
}

/* ========== 选择文本样式 ========== */
::selection {
  background: var(--primary-cyan);
  color: var(--bg-primary);
}

::-moz-selection {
  background: var(--primary-cyan);
  color: var(--bg-primary);
}

/* ========== 淡入动画 ========== */
.fade-in {
  animation: fadeIn 0.6s ease-out forwards;
  opacity: 0;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ========== 加载动画 ========== */
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loading {
  display: inline-block;
  animation: spin 1s linear infinite;
}

/* ========== 条目式列表设计 ========== */
.entry-list {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.entry-item {
  display: flex;
  gap: 0.75rem;
  padding: 0.6rem 0.75rem;
  transition: all var(--transition-fast);
  cursor: pointer;
  position: relative;
  flex-shrink: 0;
}

.entry-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: var(--gradient-primary);
  transform: scaleY(0);
  transition: transform var(--transition-normal);
}

.entry-item:hover {
  background-color: var(--panel-bg-hover);
  border-color: var(--border-strong);
  box-shadow: var(--shadow-md);
}

.entry-item:hover::before {
  transform: scaleY(1);
}

.entry-icon {
  flex-shrink: 0;
  width: 22px;
  height: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--bg-primary);
  border-radius: var(--radius-xs);
  color: var(--primary-cyan);
  font-size: 0.7rem;
  border: 1px solid var(--border-color);
  transition: all var(--transition-fast);
}

.entry-item:hover .entry-icon {
  background: var(--primary-cyan);
  color: var(--bg-primary);
  border-color: var(--primary-cyan);
}

.entry-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  min-width: 0;
}

.entry-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.entry-title {
  font-size: 0.8rem; /* 稍微调大 */
  font-weight: 600;
  color: var(--text-primary);
  flex: 1;
  min-width: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
}

.entry-status {
  padding: 0.15rem 0.4rem;
  border-radius: 3px;
  font-size: 0.65rem;
  font-weight: 600;
  text-transform: uppercase;
  flex-shrink: 0;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
}

.entry-status.completed {
  background: hsla(120, 60%, 50%, 0.2);
  color: hsl(120, 60%, 60%);
  border: 1px solid hsl(120, 60%, 50%);
}

.entry-status.in-progress {
  background: hsla(45, 80%, 50%, 0.2);
  color: hsl(45, 80%, 60%);
  border: 1px solid hsl(45, 80%, 50%);
}

.entry-status.pending {
  background: hsla(210, 60%, 50%, 0.2);
  color: hsl(210, 60%, 60%);
  border: 1px solid hsl(210, 60%, 50%);
}

.entry-status.unread {
  background: hsla(0, 70%, 50%, 0.2);
  color: hsl(0, 70%, 60%);
  border: 1px solid hsl(0, 70%, 50%);
}

.entry-status.read {
  background: hsla(210, 20%, 30%, 0.2);
  color: var(--text-tertiary);
  border: 1px solid var(--border-color);
}

.entry-meta {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  flex-wrap: wrap;
  font-size: 0.65rem;
  color: var(--text-tertiary);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
}

.entry-time,
.entry-date,
.entry-sender {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.entry-time::before,
.entry-date::before {
  content: '\f017';
  font-family: 'Font Awesome 6 Free';
  font-weight: 900;
  font-size: 0.75rem;
  opacity: 0.6;
}

.entry-sender::before {
  content: '\f007';
  font-family: 'Font Awesome 6 Free';
  font-weight: 900;
  font-size: 0.75rem;
  opacity: 0.6;
}

.entry-priority {
  padding: 0.15rem 0.35rem;
  border-radius: 3px;
  font-size: 0.65rem;
  font-weight: 600;
  flex-shrink: 0;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
}

.entry-priority.priority-urgent {
  background: hsla(0, 70%, 50%, 0.2);
  color: hsl(0, 70%, 60%);
  border: 1px solid hsl(0, 70%, 50%);
}

.entry-priority.priority-high {
  background: hsla(15, 70%, 50%, 0.2);
  color: hsl(15, 70%, 60%);
  border: 1px solid hsl(15, 70%, 50%);
}

.entry-priority.priority-medium {
  background: hsla(45, 70%, 50%, 0.2);
  color: hsl(45, 70%, 60%);
  border: 1px solid hsl(45, 70%, 50%);
}

.entry-priority.priority-low {
  background: hsla(210, 40%, 50%, 0.2);
  color: hsl(210, 40%, 60%);
  border: 1px solid hsl(210, 40%, 50%);
}

.entry-description {
  font-size: 0.7rem;
  color: var(--text-secondary);
  line-height: 1.4;
  margin-top: 0.2rem;
  word-wrap: break-word;
  white-space: normal;
}

.entry-item:hover .entry-description {
  color: var(--text-primary);
}

/* ========== 通讯模块对话界面 ========== */
.communication-body {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  /* 覆盖 .module-body 的 padding，通讯模块有自己的内边距管理 */
  padding: 0 !important;
  /* 覆盖 .module-body 的 overflow-y: auto，通讯模块不需要整体滚动 */
  overflow: hidden !important;
}

.chat-messages {
  /* 使用 flex: 1 自动计算可用高度，不允许整体滚动 */
  flex: 1;
  min-height: 0;
  overflow: hidden;
  padding: 0.75rem 1rem;
  padding-bottom: 0.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.9rem;
  /* 确保消息区域不滚动，所有内容在视口内可见 */
}

.message-item {
  display: flex;
  align-items: flex-end;
  gap: 0.35rem;
  padding: 0;
  /* flex 行为由子类（.message-user 和 .message-assistant）控制 */
  min-height: 0;
}

.message-item.message-user {
  justify-content: flex-end;
  /* user 消息根据内容自适应，不占据额外空间 */
  flex: 0 1 auto;
  min-height: 0;
}

.message-item.message-assistant {
  display: flex;
  flex-direction: row;
  align-items: flex-end; /* 让总结卡片和消息框底部对齐 */
  justify-content: flex-start;
  gap: 15px;
  /* assistant 消息占据剩余的所有空间 */
  flex: 1;
  min-height: 0;
  width: 100%;
}

.message-item.message-assistant .message-bubble {
  max-width: 83%; /* 保持原有的占比 */
  flex-shrink: 0;
}

.message-item.message-assistant .message-bubble {
  /* assistant 消息的气泡应该填满父容器的高度 */
  height: 100%;
  width: 83%;
  max-width: 83%;
}

.message-bubble {
  position: relative;
  max-width: 92%;
  width: fit-content;
  overflow: hidden;
  padding: 0.75rem 1rem;
  border-radius: var(--radius-sm);
  background: var(--bg-tertiary); /* 稍深于背景，增加稳重感 */
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-sm);
  transition: all var(--transition-fast);
  display: flex;
  flex-direction: column;
  min-height: 0;
}

.message-bubble:hover {
  border-color: var(--border-strong);
}

.message-assistant .message-bubble {
  border-left: 2px solid var(--primary-cyan);
  background-color: var(--bg-tertiary);
}

.message-user .message-bubble {
  border-right: 2px solid var(--primary-indigo);
  background-color: var(--bg-tertiary);
  align-self: flex-end;
}

/* 移除所有装饰性伪元素 */
.message-bubble::before,
.message-bubble::after {
  display: none !important;
}

/* ========== 通讯模块三栏消息样式 ========== */
.message-content-three-columns {
  display: flex;
  gap: 0.5rem;
  align-items: stretch;
  flex: 1;
  min-height: 0;
  overflow: visible;
  width: auto;
  max-width: 100%;
  flex-wrap: nowrap;
  justify-content: flex-start;
}

.message-column {
  flex: 1 1 0;
  min-width: 0;
  max-width: 25vw;
  display: flex;
  flex-direction: column;
  min-height: 0;
  overflow-y: auto;
  padding: 0.5rem;
  contain: content;
  overscroll-behavior: contain;
  -webkit-overflow-scrolling: touch;
  background: hsla(220, 10%, 15%, 0.3); /* 极淡的背景区分 */
  border: 1px solid var(--border-color);
  border-radius: var(--radius-xs);
}

.message-column-header {
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--text-tertiary);
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 0.5rem;
  padding-bottom: 0.25rem;
  border-bottom: 1px solid var(--border-color);
}

.message-column-content {
  flex: 1;
  color: var(--text-primary);
  font-size: 0.9rem; /* 调大字号 */
  line-height: 1.8; /* 调宽行距 */
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'JetBrains Mono', 'Noto Sans SC', monospace;
}

.message-column::-webkit-scrollbar {
  width: 4px;
}

.message-column::-webkit-scrollbar-track {
  background: var(--bg-tertiary);
  border-radius: 2px;
}

.message-column::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 2px;
}

.message-column::-webkit-scrollbar-thumb:hover {
  background: var(--primary-cyan);
}

.message-content {
  font-size: 0.95rem; /* 调大字号 */
  line-height: 2.0; /* 调宽行距 */
  color: var(--text-primary);
  word-wrap: break-word;
  white-space: pre-wrap;
  margin-top: 0.1rem;
  font-family: 'JetBrains Mono', 'Noto Sans SC', monospace;
  width: 100%;
}

.message-time {
  font-size: 0.65rem;
  color: var(--text-tertiary);
  white-space: nowrap;
  margin-left: 0.4rem;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-align: right;
  margin-top: 0.45rem;
}

.chat-input-container {
  flex-shrink: 0;
  padding: 0.75rem 1rem;
  border-top: 1px solid var(--border-color);
  background: var(--bg-secondary);
  /* 固定高度，确保消息区域能正确计算剩余空间 */
  min-height: 54px;
  /* 确保输入框与消息区域有适当间距 */
  margin-top: auto;
}

.chat-input-wrapper {
  display: flex;
  align-items: flex-end;
  gap: 0.5rem;
  max-width: 100%;
}

.chat-input {
  flex: 1;
  min-height: 36px;
  max-height: 100px;
  padding: 0.5rem 0.75rem;
  background: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-xs);
  color: var(--text-primary);
  font-size: 0.85rem;
  font-family: inherit;
  line-height: 1.5;
  resize: none;
  overflow-y: auto;
  transition: all var(--transition-fast);
}

.chat-input:focus {
  outline: none;
  border-color: var(--primary-cyan);
  background: var(--bg-card);
}

.chat-input::placeholder {
  color: var(--text-tertiary);
}

.chat-send-btn {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--primary-cyan);
  border: none;
  border-radius: 4px;
  color: var(--bg-primary);
  font-size: 0.8rem;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.chat-send-btn:hover {
  background: var(--accent-teal);
  box-shadow: 0 0 6px var(--accent-teal);
}

.chat-send-btn:active {
  opacity: 0.8;
}

.chat-send-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* ========== Option 选项样式 ========== */
.chat-show-options-btn {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  color: var(--text-secondary);
  font-size: 0.75rem;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.chat-show-options-btn:hover {
  background-color: var(--panel-bg-hover);
  border-color: var(--border-strong);
  box-shadow: var(--shadow-sm);
  color: var(--primary-cyan);
}

.chat-show-options-btn:active {
  opacity: 0.8;
  transform: scale(0.95);
}

/* ========== 重新生成按钮 ========== */
.chat-regenerate-btn {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-xs);
  color: var(--text-secondary);
  font-size: 0.75rem;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.chat-regenerate-btn:hover {
  background-color: var(--panel-bg-hover);
  border-color: var(--border-strong);
  box-shadow: var(--shadow-sm);
  color: var(--accent-orange);
}

.chat-regenerate-btn:active {
  opacity: 0.8;
  transform: scale(0.95);
}

.chat-regenerate-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* ========== 回退按钮 ========== */
.chat-rollback-btn {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-xs);
  color: var(--text-secondary);
  font-size: 0.75rem;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.chat-rollback-btn:hover {
  background-color: var(--panel-bg-hover);
  border-color: var(--border-strong);
  box-shadow: var(--shadow-sm);
  color: var(--accent-red);
}

.chat-rollback-btn:active {
  opacity: 0.8;
  transform: scale(0.95);
}

.chat-rollback-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* ========== 收藏按钮 ========== */
.chat-favorite-btn {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-xs);
  color: var(--text-secondary);
  font-size: 0.75rem;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.chat-favorite-btn:hover {
  background-color: var(--panel-bg-hover);
  border-color: var(--border-strong);
  box-shadow: var(--shadow-sm);
  color: #ffca28;
}

.chat-favorite-btn:hover i {
  color: #ffca28;
}

.chat-favorite-btn:active {
  opacity: 0.8;
  transform: scale(0.95);
}

.chat-favorite-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* 选项浮层背景 */
.chat-options-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(15, 23, 42, 0.85);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition:
    opacity var(--transition-normal),
    visibility var(--transition-normal);
  pointer-events: none;
}

.chat-options-overlay.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

/* 选项容器（浮层内容）- 现在只用于显示标题和关闭按钮 */
.chat-options-container {
  position: absolute;
  top: 1rem;
  right: 1rem;
  border-radius: var(--radius-md);
  padding: 0.75rem 1rem;
  box-shadow:
    0 8px 32px rgba(0, 0, 0, 0.5),
    0 0 16px hsla(180, 100%, 40%, 0.2);
  display: flex;
  align-items: center;
  gap: 0.75rem;
  z-index: 1001;
  pointer-events: auto;
}

.chat-options-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.75rem;
}

.chat-options-title {
  font-size: 0.8rem;
  font-weight: 600;
  color: var(--text-primary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.chat-options-close {
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-secondary);
  font-size: 0.8rem;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.chat-options-close:hover {
  background: var(--bg-tertiary);
  border-color: var(--primary-cyan);
  color: var(--primary-cyan);
}

/* 选项列表 - 全屏定位容器 */
.chat-options-list {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: visible;
}

.chat-options-list::-webkit-scrollbar {
  width: 6px;
}

.chat-options-list::-webkit-scrollbar-track {
  background: var(--bg-tertiary);
  border-radius: 3px;
}

.chat-options-list::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 3px;
}

.chat-options-list::-webkit-scrollbar-thumb:hover {
  background: var(--primary-cyan);
}

/* ========== Chat Option 气泡（核心） ========== */

.chat-option {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  opacity: 0;
  transform: translate(-50%, -50%) scale(0.85) rotate(var(--rot));
  transition:
    opacity 0.35s ease,
    transform 0.45s cubic-bezier(0.22, 1, 0.36, 1),
    filter 0.3s ease;

  animation: thought-float 4.5s ease-in-out infinite;
}

/* 念头浮现 */
.chat-option.show {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1) rotate(var(--rot));
}

/* 轻微漂浮 */
@keyframes thought-float {
  0% {
    transform: translate(-50%, -52%) rotate(var(--rot));
  }
  50% {
    transform: translate(-50%, -48%) rotate(var(--rot));
  }
  100% {
    transform: translate(-50%, -52%) rotate(var(--rot));
  }
}

/* 真正的"选项" - 升级为更加严谨的 HUD 风格 */
.chat-option-button {
  position: relative;
  width: 130px;
  height: 130px;
  aspect-ratio: 1 / 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  border-radius: var(--radius-xs); /* 使用统一的小圆角 */
  overflow: visible;

  background: var(--bg-card);
  border: 1px solid var(--border-color);
  color: var(--text-primary);

  cursor: pointer;
  box-shadow: var(--shadow-md);
  backdrop-filter: blur(4px);
  will-change: transform;
  transition: all var(--transition-normal);
}

/* Hover = 锐利的高亮 */
.chat-option-button:hover {
  transform: translateY(-5px);
  background: var(--bg-tertiary);
  border-color: var(--primary-cyan);
  box-shadow:
    0 8px 20px rgba(0, 0, 0, 0.4),
    0 0 0 1px var(--primary-cyan);
}

/* 点击 */
.chat-option-button:active {
  transform: translateY(-2px) scale(0.98);
}

/* 装饰性元素 - 模拟传感器或接口，增加科技感而不显廉价 */
.chat-option-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  border-top: 2px solid var(--primary-cyan);
  border-left: 2px solid var(--primary-cyan);
  opacity: 0.5;
}

.chat-option-button::after {
  content: '';
  position: absolute;
  bottom: 0;
  right: 0;
  width: 10px;
  height: 10px;
  border-bottom: 2px solid var(--primary-cyan);
  border-right: 2px solid var(--primary-cyan);
  opacity: 0.5;
}

/* 文字样式 - 针对圆形气泡优化 */
.chat-option-text {
  font-size: 0.85rem; /* 稍微调大 */
  line-height: 1.3;
  text-align: center;
  width: 100%;
  max-height: 80%; /* 限制高度 */

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3; /* 增加到3行 */
  line-clamp: 3;

  overflow: hidden;
  word-break: break-word;
  pointer-events: none;
  color: var(--text-primary);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  font-weight: 500;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.8); /* 增强阴影，提升文字在磨砂玻璃上的辨识度 */
  padding: 0 0.8rem; /* 稍微增加内边距 */
}

/* hover：聚焦一个念头
   关键点：不要给 `.chat-option`（它在做 transform 动画）直接加 blur filter，
   否则在部分 WebView/Electron 下会出现“filter + transform 动画导致不绘制”的现象（看起来像消失）。
   这里把 blur 下放到 `.chat-option-button` 上，父容器只做 opacity。 */
.chat-options-list:has(.chat-option:hover) .chat-option:not(:hover) {
  opacity: 0.75; /* 只降低透明度，不直接 blur 容器 */
}
.chat-options-list:has(.chat-option:hover) .chat-option:not(:hover) .chat-option-button {
  filter: blur(3px) grayscale(0.2) brightness(0.95);
}

/* 兼容性回退：如果浏览器不支持 :has()，使用父容器 hover（依赖 list 可 hover） */
@supports not selector(:has(*)) {
  .chat-options-list:hover .chat-option:not(:hover) {
    opacity: 0.75;
  }
  .chat-options-list:hover .chat-option:not(:hover) .chat-option-button {
    filter: blur(3px) grayscale(0.2) brightness(0.95);
  }
}

.chat-option:hover {
  z-index: 10;
  opacity: 1 !important; /* 确保悬停项完全清晰 */
  filter: none !important;
}

.chat-option:hover .chat-option-button {
  filter: brightness(1.2) contrast(1.1); /* 进一步增强悬停项的视觉表现 */
  transform: scale(1.12);
  box-shadow:
    0 12px 32px rgba(0, 0, 0, 0.4),
    0 0 25px hsla(180, 100%, 40%, 0.5);
}

/* 被选中的念头：膨胀并高亮 */
.chat-option-button.selected {
  transform: scale(1.4);
  background: var(--primary-cyan);
  border-color: white;
  color: white;
  box-shadow: 0 0 40px hsla(180, 100%, 40%, 0.6);
  z-index: 100;
  animation: none;
}

.chat-option-button.selected .chat-option-text {
  color: white;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
}

/* 其他念头变模糊而不是完全消失 */
.chat-options-list.collapse .chat-option:not(.selected-parent) {
  opacity: 0.3; /* 保持微弱可见 */
  /* 同 hover 一样，避免给动画容器加 blur，改为 blur button */
  transform: translate(-50%, -50%) scale(0.85);
  pointer-events: none;
}
.chat-options-list.collapse .chat-option:not(.selected-parent) .chat-option-button {
  filter: blur(6px) grayscale(0.5) brightness(0.9);
}

.chat-option.selected-parent {
  z-index: 20;
}

/* 无选项提示消息 */
.chat-no-options-message {
  width: 100%;
  padding: 1rem;
}

.chat-no-options-content {
  border-radius: var(--radius-md);
  padding: 1rem 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

.chat-no-options-text {
  color: var(--text-primary);
  font-size: 0.95rem; /* 稍微调大 */
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
}

.chat-no-options-close {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 1px solid var(--border-color);
  border-radius: 50%;
  color: var(--text-secondary);
  font-size: 1.2rem;
  cursor: pointer;
  transition: all var(--transition-fast);
  flex-shrink: 0;
}

.chat-no-options-close:hover {
  background: var(--bg-tertiary);
  border-color: var(--primary-cyan);
  color: var(--primary-cyan);
}

/* ========== Summary Card 样式 ========== */
.summary-card {
  flex-shrink: 0;
  width: 180px; /* 较窄的宽度，适合放在右侧空白处 */
  margin-bottom: 0.8rem; /* 底部与气泡对齐对齐 */
  background: var(--bg-glass);
  border: 1px solid var(--border-color);
  border-right: 2px solid var(--primary-cyan);
  border-radius: var(--radius-xs);
  padding: 0.6rem;
  box-shadow: var(--shadow-md);
  z-index: 5;
  animation: fadeIn 0.5s ease-out;
  backdrop-filter: blur(10px);
  height: fit-content;
}

.summary-content {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}

.summary-item {
  display: flex;
  flex-direction: column;
  gap: 0.1rem;
}

.summary-label {
  font-size: 0.65rem; /* 稍微调大 */
  color: var(--primary-cyan);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.5px;
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

.summary-label i {
  font-size: 0.7rem;
}

.summary-value {
  font-size: 0.8rem; /* 调大字号 */
  color: var(--text-secondary);
  line-height: 1.5; /* 增加行距 */
  word-break: break-word;
  padding-left: 0.2rem;
}

/* ========== 流式输出样式 ========== */
.message-streaming {
  opacity: 1; /* 保持最高亮度 */
}

.streaming-indicator {
  display: flex;
  align-items: center;
  gap: 0.4rem;
  margin-bottom: 0.3rem;
  color: var(--primary-cyan);
  font-size: 0.75rem; /* 稍微调大 */
}

.streaming-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: var(--primary-cyan);
  animation: streaming-pulse 1.5s ease-in-out infinite;
}

@keyframes streaming-pulse {
  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.5;
    transform: scale(0.8);
  }
}

.streaming-text {
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  font-size: 0.75rem; /* 稍微调大 */
}

.streaming-text-content {
  color: var(--text-primary);
  font-size: 0.9rem; /* 调大字号 */
  line-height: 1.8; /* 增加行距 */
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'JetBrains Mono', 'Noto Sans SC', monospace;
  margin-top: 0.2rem;
}

/* ========== 网 (The Net) 模块样式 ========== */
.net-body {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
}

.net-tabs {
  display: flex;
  gap: 0.5rem;
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 0.5rem;
}

.net-tab {
  padding: 0.5rem 1rem;
  font-size: 0.85rem; /* 稍微调大 */
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: var(--text-tertiary);
  cursor: pointer;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  transition: all var(--transition-fast);
}

.net-tab:hover {
  background: var(--bg-tertiary);
  color: var(--text-primary);
}

.net-tab.active {
  background: var(--primary-cyan);
  color: var(--bg-primary);
  border-color: var(--primary-cyan);
  font-weight: 700;
}

.net-content-area {
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.net-empty-text {
  padding: 2rem;
  text-align: center;
  color: var(--text-tertiary);
  font-family: 'Courier New', monospace;
  font-size: 0.8rem;
}

.net-item-card {
  background: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-sm);
  padding: 1rem;
  transition: all var(--transition-fast);
}

.net-item-card:hover {
  border-color: var(--primary-cyan);
  transform: translateX(4px);
}

.net-item-header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 0.5rem;
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 0.3rem;
}

.net-item-title {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--primary-cyan);
}

.net-item-author {
  font-size: 0.7rem;
  color: var(--text-tertiary);
  font-family: 'Courier New', monospace;
}

.net-item-body {
  font-size: 0.85rem;
  color: var(--text-secondary);
  line-height: 1.6;
  white-space: pre-wrap;
}

.net-report-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.net-report-editor {
  flex: 1;
  min-height: 200px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-strong);
  border-radius: var(--radius-sm);
  padding: 1rem;
  color: var(--text-primary);
  font-size: 0.9rem;
  font-family: 'Courier New', monospace;
  line-height: 1.6;
  resize: none;
  outline: none;
  transition: all var(--transition-fast);
}

.net-report-editor:focus {
  border-color: var(--primary-cyan);
  box-shadow: 0 0 10px hsla(190, 50%, 82%, 0.1);
}

/* ========== 历史记录编辑器 ========== */
.net-history-editor {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.net-history-title-input {
  background: transparent;
  border: none;
  border-bottom: 1px solid var(--border-color);
  color: var(--primary-cyan);
  font-size: 1.5rem;
  font-weight: bold;
  font-family: 'Michroma', 'Noto Sans SC', sans-serif;
  width: 100%;
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
}

.net-history-title-input:focus {
  outline: none;
  border-bottom-color: var(--primary-cyan);
}

.net-history-author-input {
  background: transparent;
  border: none;
  border-bottom: 1px thin transparent;
  color: var(--text-tertiary);
  font-size: 0.85rem;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
  width: auto;
  padding: 0 2px;
  transition: all var(--transition-fast);
}

.net-history-author-input:hover,
.net-history-author-input:focus {
  outline: none;
  border-bottom-color: var(--text-tertiary);
  background: rgba(255, 255, 255, 0.05);
}

.net-history-content-editor {
  flex: 1;
  background: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-sm);
  color: var(--text-primary);
  font-family: 'Inter', 'Noto Sans SC', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  padding: 1rem;
  margin-top: 1rem;
  resize: none;
}

.net-history-content-editor:focus {
  outline: none;
  border-color: var(--primary-cyan);
}

/* 悬浮操作容器 */
.net-floating-actions {
  position: absolute;
  bottom: 20px;
  right: 20px;
  display: flex;
  gap: 12px;
  z-index: 10;
}

/* 悬浮保存按钮 */
.net-floating-save-btn {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background: var(--primary-cyan);
  color: #000;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: var(--shadow-lg);
  transition: all var(--transition-fast);
}

.net-floating-save-btn:hover {
  transform: translateY(-3px) scale(1.05);
  background: var(--accent-teal);
  box-shadow: 0 10px 20px rgba(6, 182, 212, 0.3);
}

/* 悬浮删除按钮 */
.net-floating-delete-btn {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background: rgba(239, 68, 68, 0.1);
  color: var(--accent-red);
  border: 1px solid rgba(239, 68, 68, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: var(--shadow-md);
  transition: all var(--transition-fast);
}

.net-floating-delete-btn:hover {
  transform: translateY(-3px) scale(1.05);
  background: var(--accent-red);
  color: #fff;
  border-color: transparent;
  box-shadow: 0 10px 20px rgba(239, 68, 68, 0.3);
}

.net-floating-save-btn i,
.net-floating-delete-btn i {
  font-size: 1.2rem;
}

.net-floating-save-btn.saving,
.net-floating-delete-btn.deleting {
  background: var(--text-tertiary);
  cursor: wait;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
}

.net-floating-save-btn:disabled,
.net-floating-delete-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* ========== 网 (The Net) VSCode 风格设计 ========== */
.net-body {
  padding: 0 !important;
  height: 100%;
}

.net-explorer {
  display: flex;
  height: 100%;
  width: 100%;
}

.net-sidebar {
  width: 240px;
  background: var(--bg-secondary);
  border-right: 1px solid var(--border-color);
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  flex-shrink: 0;
}

.net-viewer {
  flex: 1;
  background: var(--bg-primary);
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.net-sidebar-section {
  user-select: none;
}

.net-sidebar-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.4rem 0.75rem;
  background: var(--bg-tertiary);
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: var(--text-secondary);
  cursor: pointer;
  border-bottom: 1px solid var(--border-color);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
}

.net-sidebar-header:hover {
  background: var(--panel-bg-hover);
  color: var(--text-primary);
}

.net-sidebar-header i {
  width: 12px;
  font-size: 0.7rem;
  color: var(--text-tertiary);
}

.net-sidebar-items {
  display: flex;
  flex-direction: column;
}

.net-sidebar-item {
  display: flex;
  align-items: center;
  gap: 0.6rem;
  padding: 0.35rem 1.5rem;
  font-size: 0.8rem;
  color: var(--text-tertiary);
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: all var(--transition-fast);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
}

.net-sidebar-item:hover {
  background: var(--panel-bg-hover);
  color: var(--text-primary);
}

.net-sidebar-item.active {
  background: hsla(190, 50%, 82%, 0.1);
  color: var(--primary-cyan);
  border-left: 2px solid var(--primary-cyan);
  padding-left: calc(1.5rem - 2px);
}

.net-sidebar-item i {
  font-size: 0.8rem;
  opacity: 0.7;
}

.net-item-viewer {
  padding: 2rem;
  overflow-y: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  position: relative;
}

.net-viewer-header {
  border-bottom: 1px solid var(--border-strong);
  padding-bottom: 1rem;
}

.net-viewer-title {
  font-size: 1.6rem; /* 稍微调大 */
  color: var(--primary-cyan);
  margin-bottom: 0.5rem;
  font-family: 'Michroma', 'Noto Sans SC', sans-serif;
}

.net-viewer-meta {
  font-size: 0.85rem; /* 稍微调大 */
  color: var(--text-tertiary);
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
}

.net-viewer-content {
  font-size: 1rem;
  line-height: 2.0; /* 调宽行距 */
  color: var(--text-primary);
  word-wrap: break-word;
  white-space: pre-wrap;
  font-family: 'Inter', 'Noto Sans SC', sans-serif;
}

.net-viewer-content h1,
.net-viewer-content h2,
.net-viewer-content h3 {
  color: var(--primary-cyan);
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
  font-family: 'Michroma', 'Noto Sans SC', sans-serif;
}

.net-viewer-content p {
  margin-bottom: 1rem;
}

.net-viewer-content ul,
.net-viewer-content ol {
  margin-left: 1.5rem;
  margin-bottom: 1rem;
}

.net-viewer-content li {
  margin-bottom: 0.5rem;
}

.net-viewer-content code {
  background: var(--bg-tertiary);
  padding: 0.2rem 0.4rem;
  border-radius: 3px;
  font-family: 'JetBrains Mono', 'Noto Sans SC', monospace;
  font-size: 0.9em;
  color: var(--accent-teal);
}

.net-viewer-content pre {
  background: var(--bg-tertiary);
  padding: 1rem;
  border-radius: 4px;
  overflow-x: auto;
  margin-bottom: 1rem;
  border: 1px solid var(--border-color);
}

.net-viewer-content pre code {
  background: transparent;
  padding: 0;
  color: var(--text-secondary);
}

.net-viewer-content blockquote {
  border-left: 3px solid var(--primary-cyan);
  padding-left: 1rem;
  margin-left: 0;
  margin-bottom: 1rem;
  color: var(--text-tertiary);
  font-style: italic;
}

.net-viewer-content table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1rem;
}

.net-viewer-content th,
.net-viewer-content td {
  border: 1px solid var(--border-color);
  padding: 0.5rem;
  text-align: left;
}

.net-viewer-content th {
  background: var(--bg-tertiary);
  color: var(--primary-cyan);
}

.net-viewer-empty {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: var(--text-tertiary);
  font-style: italic;
  font-size: 0.9rem;
  font-family: 'Space Grotesk', 'Noto Sans SC', sans-serif;
}

.net-report-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
}

.net-report-editor {
  flex: 1;
  margin-top: 1rem;
  background: var(--bg-card) !important;
  border: 1px solid var(--border-strong) !important;
  border-radius: var(--radius-sm);
  padding: 1.5rem;
  color: var(--text-primary);
  font-size: 1rem;
  font-family: 'JetBrains Mono', 'Noto Sans SC', monospace;
  line-height: 1.8;
  resize: none;
  outline: none;
}
